.feadback-flow{
display: flex;
flex-flow: row wrap;
list-style: none;
}
.student-header{
  margin: 20px 0;
}
.feadback-rating progress {
  border: none;
  display: block;
  width: 250px;
  margin: 20px;
  height: 10px;
}
progress::-webkit-progress-value {
background: #6a6f73;
}
progress::-webkit-progress-bar {
background-color: #d1d7dc; width: 100%;
}
.checked {
color: orange;
}
.feadback-img{
font-size: 80px;
color: #b4690e;
font-weight: bold;

}
.feedback-summary{
margin-top: -5px;
}
.feadback-flow p{
font-size: 15px;
color: #b4690e;
font-weight: bold;
}
.feadback-rating-flow2{
 display: block;
 margin-top: 12px;
font-size: 1.1rem;
}
.feadback-rating-flow2 span{
margin-bottom: 8px;
}
.feadback-rating-flow2 a{
color: #5624d0;
}
.feedback-star{
  font-size: 1rem;
}
/* end of feedback  */
/* start of course cart */
.course-cart{
  width: 350px;
  background-color: white;
  font-size: 1rem;
  color: black;
  border: #1c1d1f 1px solid;
  z-index: 3;
  position: absolute;
  top: 100px;
  right: 5%;
}
.cart-img{
  width: 100%;
}
.cart-img img{
  width: 100%;
}

.cart-price{
  color: #1c1d1f;
  font-size: 2rem;
  font-weight: bold;
}
.discound-price{
  color: #6a6f73;
  text-decoration: line-through;
}
.cart-off{
  color: #1c1d1f;
  display: block;
  font-size: 1rem;
}
.cart-last-price{
  color: #b32d0f;
  font-size: 0.9rem;
}
.discound-cart{
  margin-bottom: 12px;
}
.add-to-cart{
  width: 100%;
  height: 3rem;
  margin-top: 2%;
  background-color: #8710d8;
  border: none;
  outline: none;
  font-size: 1rem;
  font-weight: 700;
  color: white;
}
.buy-now{
  width: 100%;
  height: 3rem;
  margin-top: 2%;
  background-color: transparent;
  border: 1px solid #1c1d1f;
  font-size: 1rem;
  font-weight: 700;
}
.cart-container{
  width: 90%;
}
.money-back{
  font-size: 0.8rem;
  font-weight: 400;
  margin: 20px 0;
  text-align: center;
}
.course-cart-data-include h3{
  font-size: 1rem;
  font-weight:bold;
}
.course-cart-data-include ul{
  list-style: none;
  margin: 0;
}
.course-cart-data-include li{
  margin-left: -1.7rem;
  font-size: 0.9rem;
  font-weight: 400;
  margin-bottom: 10px;
}
.course-cart-data-include li i{
padding-right: 5px;
color: #2d3236;
width: 20px;
}
.share-gift-coupon a{
text-decoration: none;
color: black;
font-size: 0.88rem;
font-weight: bold;
padding-bottom: 2px;
border-bottom: #1c1d1f 1px solid;
letter-spacing: -0.02rem;
}
.share-gift-coupon{
display: flex;
justify-content: space-between;
}
.share-gift-coupon button{
text-decoration: none;
background: transparent;
outline: none;
border: none;
}
.cart-container h4{
font-size: 1.2rem;
font-weight: bold;
}
.cart-container p{
font-size: 0.9rem;
font-weight: 400;
}
/* end of course cart */

/* start of footer */
footer{
background: #1c1d1f;
border-top: 1px solid #3e4143;
color: #fff;
padding: 0 40px;
font-size: 0.9rem;
}
.footer-secsion1{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; 
  border-color: white;
  font-size: 0.9rem;
}
.footer-line{
margin: 30px -35px 30px -35px;
}
.footer-secsion1 img{
  margin: 5px;
}
.footer-secsion1-div1{
font-family: udemy sans,-apple-system,BlinkMacSystemFont,Roboto,segoe ui,Helvetica,Arial,sans-serif,apple color emoji,segoe ui emoji,segoe ui symbol;
font-weight: 700;
letter-spacing: -0.0rem;
font-size: 1.2rem; 
}
.footer-secsion1-div1 a{
color: #cec0fc;
text-decoration: underline;
}
.footer-secsion2{
display: flex;
justify-content: space-between;
height: 200px;
}
.footer-secsion2 ul{

  margin-right: 1.6rem;
  margin-bottom: 0;
  list-style: none;
  margin: 0;
  padding: 0; 
  max-width: none;
}
.footer-secsion2 li a{
color: white;
}
.footer-secsion3 {
 display: flex;
 justify-content: space-between;
}
.footer-secsion3 img{
height: 35px;
background-color: transparent;
margin-bottom: 100px;
}
.footer-secsion3 span{
color: white;
font-size: 0.9rem;
}
.footer-secsion2 button{
 background-color: transparent;
border-color: wheat;
height: 40px;
width: 150px;
}
.footer-secsion2 button span{
color: white;
}
.footer-secsion2 button i{
color: white;
}
/* end of footer */
.display-block{
  display: inline-block;
}
.responsive{
  color:white;
  display: none;
}
.responsive-img{
  width: 100%;
  margin-bottom: 30px;
}
@media only screen and (max-width: 1030px) {
  .description {
    width: 80%;
    margin: auto;
    padding: auto;
  }
  .explore1 {
    margin: auto;
    padding: auto;
    margin-bottom: 30px;
  }
  .container-flex {
    justify-content: center;
  }
}
@media only screen and (max-width: 950px) {
  .cart1 {
    width: 28%;
  }
  .footer-secsion1 img{
    height: 40px;
  }
}
@media only screen and (max-width: 550px) {
  .cart1 {
    width: 50%;
  }
}

@media only screen and (max-width: 1130px) {
  .business {
    display: none;
  }
  .course-cart{
    display: none;
  }
  .cart-price{
    color: white;
  }
  .responsive{
    display: inline-block;
  }
  .header-media-link{
    margin-top: 50px;
    color: white;
    text-align: center;
    justify-content: space-around;
    display: flex;
    text-decoration: underline;
  }
  .header-media-link a{ color: white;}
  .display-block{
    display: block;
  }
  .money-back{
    display: block;
  }
  .width-25{
    width: 25%;
  }
  .second-navbar{
    bottom: 0;
    top: unset;
  }
  .btn-header{
    font-size: 0.8rem;
  }
}
@media only screen and (max-width: 900px) {
  .teach {
    display: none;
  }
  .header-paragraph {
    position: relative;
    width: 70%;
    margin: 0;
    padding: 0;
    text-align: center;
    left: auto;
    top: auto;
  }
  .head-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (max-width: 700px) {
  .nav-media {
    display: none;
  }
  .menu1 {
    display: block;
    margin-left: 5%;
    margin-right: 55%;
  }
}
.seemore,.showmore,.showmore2{
  display: none;
}

.more-content{
  border: 1px solid #1c1d1f;
  height: 3rem;
  color: #1c1d1f;
  font-weight: bold;
  background-color: transparent;
  margin-top: 1.2rem;
  width: 100%;
  text-align: center;
}
.more-content:hover{
  color: #1c1d1f;
  background-color: rgba(0,0,0,.04);
}
